import * as LegendaryUpdater  from "./Legendary";
import * as BackstageUpdater from "./Backstage";
import * as CheeseUpdater from "./Cheese";
import * as NoramlUpdater from "./Noraml";

const UPDATES_OBJS = [
    LegendaryUpdater,
    BackstageUpdater,
    CheeseUpdater 
];
export const getUpdateFor = function (item) {
    return UPDATES_OBJS.map(updater => updater.isUsefulFor(item)) || NoramlUpdater;
};
